import React from "react";
import styled from "styled-components";
import { useRecoilValue } from "recoil";
import { selectedSettlementState } from "@states/accordionAtoms";

export const Detail = () => {
  const selectedSettlement = useRecoilValue(selectedSettlementState);

  return (
    <Wrapper>
      {selectedSettlement ? (
        <CardContent>
          <h2>{selectedSettlement.name}</h2>
          <div>{selectedSettlement.image}</div>
          <p>{selectedSettlement.description}</p>
        </CardContent>
      ) : (
        <CardContent>
          <p>error component</p>
        </CardContent>
      )}
    </Wrapper>
  );
};

export default Detail;

const Wrapper = styled.div``;
const CardContent = styled.div``;
