import React from "react";
import styled from "styled-components";
import { useSetRecoilState } from "recoil";
import { selectedSettlementState } from "@states/accordionAtoms";
import { cardOpenState } from "@states/accordionAtoms";
import { SettlementData } from "../../../types/settlement";

const data: SettlementData[] = [
  {
    name: "  1",
    image: " ",
    description: " 1",
  },
  {
    name: "  2",
    image: " ",
    description: " 2",
  },
  {
    name: "  3",
    image: " ",
    description: " 3",
  },
];

export const Result = () => {
  const setSelectedSettlement = useSetRecoilState(selectedSettlementState);
  const setCardOpen = useSetRecoilState(cardOpenState);

  const handleCardClick = (item: any) => {
    setSelectedSettlement(item);
    setCardOpen(true);
  };

  return (
    <Wrapper>
      {data.map((item, index) => (
        <CardWrapper key={index} onClick={() => handleCardClick(item)}>
          <div>{item.name}</div>
          <div>{item.image}</div>
          <div>{item.description}</div>
        </CardWrapper>
      ))}
    </Wrapper>
  );
};

export default Result;

const Wrapper = styled.div`
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 30px;
`;
const CardWrapper = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-shadow: ${(props) => props.theme.boxShadow};
  margin: 10px;
  height: 150px;
  width: 90%;
`;
